<template>
    <section id="before-poland" class="w-100 h-100">
      <Carousel carouselId="before-poland" :componentLink="componentLink" :sliderWithCaption="SliderData" :lang="this.lang" />
      <DirectionArrow arrowPosition="bottom" :componentLink="componentLink"/>
    </section>
</template>
<script>
import Carousel from "@/components/Carousel/Carousel";
import DirectionArrow from "@/components/Layout/DirectionArrow";
import {SECOND_WAR_SLIDER} from "@/assets/js/Consts/SectionBeforePoland/BeforePolandSlider";

export default {
   data() {
    return {
      SliderData: SECOND_WAR_SLIDER[this.lang],
    }
  },
  updated() {
    this.SliderData = SECOND_WAR_SLIDER[this.lang];
  },
  props: ['componentLink', 'lang'],
  components: {Carousel,DirectionArrow},
}
</script>