<template>
    <div v-if="entered" :class="this.isDesktop()?'container-fluid ':'container-fluid px-0'">
      {{ this.runPreloader() }}
      <div v-if="this.isDesktop()" class="row">
        <Sidebar :pages="PAGES" :lang="this.lang"/>
        <div class="col-12 px-0">
          <div id="main">
            <Home :pages="PAGES" :lang="this.lang"/>
          </div>
        </div>
      </div>
      <Mobile v-else/>
    </div>
    <Start v-else/>
</template>

<script>
import {defineAsyncComponent} from "vue";
import Sidebar from "@/components/Layout/Sidebar";
import Start from "@/components/Layout/Start";
import {Pages} from "@/assets/js/Consts/Pages"
import Home from "@/components/Pages/Content"

const Mobile = defineAsyncComponent(() => import('@/components/Mobile/Mobile'));

export default {
  name: 'App',
  components: {
    Mobile,
    Sidebar,
    Home,
    Start,
  },
  data() {
    return {
      PAGES: Pages,
      lang: this.$store.state.lang,
      entered: this.$store.state.entered,
      prealoder: true
    };
  },
  methods: {
    isDesktop() {
      return window.innerWidth > 1200;
    },
    runPreloader() {
      let loader = this.$loading.show(
              {
                backgroundColor: '#000',
                opacity: 1,
                color: '#fff',
                loader: 'dots'
              }
      );
      setTimeout(() => {
        loader.hide();
      }, 1000);
    }
  },
  watch: {
    '$store.state.entered': function (entered) {
      this.entered = entered;
    },
    '$store.state.lang': function (lang) {
      this.lang = lang;
    },
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Brygada+1918&display=swap');

html,
body {
  margin: 0;
  box-sizing: border-box;
  background: #202020 !important;
}

#app {
  font-family: 'Brygada 1918';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100vh;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none !important;
}

.btn:focus, .btn:active {
  outline: none !important;
  box-shadow: none !important;
}
</style>
